
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
html{
    font-size: 16px;
}
.filter{
    min-height:20vh;
    background-color: rgb(255, 17, 17);
    color: black;
}
.list{
    min-height: 80vh;
}
.app{
    font-family: Arial, Helvetica, sans-serif;
    font-size:20px;
}
.card img{
    min-width: 70%;
}
.types{
    margin: 5px;
}
.types span{
    font-size: 12px;
    padding: 3px 5px;
    margin:2px;
    border-radius: 5px;
    font-weight: bold;
}
.card h3{
    font-size: 1em;
    margin-bottom: -10px;
    font-family: 'Baloo Chettan', cursive;
}
.list{
    padding: 30px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-justify-content: space-around;
            justify-content: space-around;
}
.card{
    -webkit-flex-basis: 20%;
            flex-basis: 20%;
    margin: 10px 15px 50px 15px;
    position: relative;
    text-align: center;
}

.filter{
    padding: 20px;
}
.filter input, .filter select{
    border-radius: 10px;
    border:none;
    padding: 5px;
    font-size: 1.2rem;
    margin: 5px 5px 5px 10px;
}
.filter h2{
    margin-bottom: 10px;
    font-size: 2em;
    font-family: 'Baloo Chettan', cursive;
}
.filter label{
    font-weight: bold;
    margin-left: 5%;
    display: block;
}

